import React, {CSSProperties, useState} from 'react';
import _ from 'lodash';
import {hot} from "react-hot-loader";
import {Button, Card, Input, Page} from "react-onsenui";
import {DonateParam, DonationInfo} from "@web3game/typings/home";
import {getHomeAPI} from "@web3game/apis/home/getHomeAPI";
import {popPage} from "@web3game/common/utils/navigatorHelper";
import {getLang} from "@web3game/lang";
import {withAPI} from "@web3game/common/components/withAPI";
import {compose} from '@web3game/common/utils/compose';
import {inject} from 'mobx-react';
import {Str} from "../elements";
import {createHeaderRender} from "../Header";


type DonatePageProps = {
    donationInfo: DonationInfo;
    onDonate: (p: DonateParam) => Promise<void>;
};

const styles = {
    input: {
        width: '100%',
    },
    label: {
        fontSize: 12,
    },
    button: { float: 'right' },
};

export const DonatePagePure = React.memo(
    (
        {
            donationInfo,
            onDonate,
        }: DonatePageProps,
    ) => {

        const [ttAmount, setTTAmount] = useState('');
        const handleDonate = () => onDonate({
            ttAmount: Number(ttAmount),
        });

        return (
            <Page renderFixed={createHeaderRender(getLang().DONATE.DONATE)}>
                <Card>
                    <Str>
                        {getLang().DONATE.CURRENT_DAY}
                        {donationInfo && donationInfo.currentDay}
                        {getLang().DONATE.DAY}
                    </Str>
                    <br />
                    <Str>
                        {getLang().DONATE.RATE}
                        1TT = {donationInfo && donationInfo.rate}TTi
                    </Str>
                    <br />
                    <Str>
                        {getLang().DONATE.REMAIN_TOKEN_TODAY}
                        {donationInfo && donationInfo.remainTokenOfDay}
                    </Str>
                    <br />
                    <Str>
                        {getLang().DONATE.TOKEN_TODAY}
                        {donationInfo && donationInfo.tokenOfDay}
                    </Str>
                    <br />
                    <Str>{getLang().DONATE.TT_LIMIT}{donationInfo && donationInfo.ttLimit}</Str>
                    <br />
                    <br />
                    <Input
                        placeholder={getLang().DONATE.TT_AMOUNT}
                        float
                        modifier="material"
                        style={styles.input}
                        type="number"
                        value={ttAmount}
                        onChange={e => setTTAmount(e.target.value)}
                    />
                    <br />
                    <br />

                    <Button style={styles.button as CSSProperties} onClick={handleDonate}>
                        {getLang().DONATE.DONATE}
                    </Button>

                    <br />
                    <br />

                </Card>
            </Page>
        );
    },
);

export const DonatePage = compose<DonatePageProps>(
    hot(module),
    inject(({accountStore}) => {
        return {
            updateDataVersion: () => accountStore.updateDataVersion(),
        };
    }),
    withAPI(
        async ({updateDataVersion}) => {
            const homeAPI = await getHomeAPI();
            const donationInfo = await homeAPI.getDonationInfo();
            const onDonate = async (params: DonateParam) => {
                try {
                    await homeAPI.doDonate(params);
                    updateDataVersion();
                    popPage();
                } catch (e) {
                    alert(e.message);
                }
            };
    
            return {
                donationInfo,
                onDonate,
            };
        },
    ),
)(DonatePagePure);

export default DonatePage;
